import React from 'react';
import styles from './subline.css';
import classnames from 'classnames';

type Props = {
  small?: boolean;
  children: string;
};

export default function Subline({ children, small }: Props) {
  return (
    <span className={classnames(styles.subline, small && styles.small)}>
      {children}
    </span>
  );
}
